@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar {
  width: 100vw;
  background: #1c3e6f;
}

.nav-item a {
  color: white;
  font-weight: bold;
  font-size: 20px;
  margin-right: 20px;
}

.nav-item a:hover {
  color: #c4d1e4;
}

.navbar-brand img {
  margin-left: 20px;
  max-width: 40px;
}
/* make the text go down */
.body_text {
  font-family: 'Times New Roman', Times, serif;
  position: relative;
  font-size: 150%;
  margin-left: 40px;
  margin-right: 50px;
  margin-top: 170px;
  margin-bottom: 10px;

}

*{
  font-family: 'Fredoka One', cursive;
  letter-spacing: 2px !important;
  color: #1C3E6E;
}

p {
  color: #8198B8;
}

span label {
  text-align: center; 
}

.row h5 {
  padding-left: 5%;
}

.course-description {
  font-size: smaller;
  color: #35598b;
  margin-right: 2%;
}

.course-general-info .row {
  padding-left: 5%;
  font-size: smaller;
  color: #8198B8;
}

.page-content {
  background-color: #F2F4F8;
}

.course-template {
  padding: 2% 0;
}

.course-template .course-title {
  padding-top: 5%;;
  padding-bottom: 3%;
}

.col-item {
  background-color: white;
  border-radius: 15px;
  margin-right: 10px;
  margin-left: 10px;
  margin-top: 10px;
  margin-bottom: 2%;
  padding-bottom: 2%;
}

.course-template .syllabus {
  vertical-align: center;
  padding-top: 3%;
  padding-bottom: 3%;
}

.course-template .col-item h3 {
  margin-top: 30px;

}

.course-template .col-item h4 {
  margin-top: 5%;

}

.course-template .col-item p {
  padding: 1% 5%;
  color: #5C779C;
}

.course-template .course-description, .course-requisite {
  margin-left: 0;
}

.requisites-display {
  margin-top: 2% !important;
  background-color: #EFF3FB;
  margin-left: 3%;
  border-radius: 15px;
  padding-top: 5px;
  padding-bottom: 5px;
  margin-bottom: 2%;
}

.req-list {
  list-style: none;
  -webkit-padding-start: 0;
          padding-inline-start: 0;
  display: inline-block;
  padding: 2% 5%;
}

.req-list ::after {
  content: ', '; 
}

.req-list :last-child:after {
  content: "";
}

.req-list a {
  color: #5C779C;
  text-decoration: none;
}


.requisites-display.corequisites {
  background-color: #DFF0CA;
}

.requisites-display.exclusions {
  background-color: #eec1be;
}

.expand_button {
  margin-top: -5%;
}

.syllabus-link {
  margin-top: 2%;
    border-radius: 15px;
    background-color: #1C3E6E;
    color: white;
    width: 8em;
    height: 3em;
    border: none;
    font-size: 1.2em;
    text-align: center;
}

.req-graph {
  border: none;
  width: 60%;
  margin-left: 5%;
  justify-content: center;
  margin-bottom: 2%;
}

.course-offering table {
  width: 100%;
  margin-top: 2%;
}

.course-offering tr th:nth-child(4),tr td:nth-child(4), tr th:nth-child(6),tr td:nth-child(6), tr th:nth-child(7),tr td:nth-child(7){  
  display:none; 
}
p {
  padding: 0% 10%;
  overflow-wrap: break-word;
}

.result-display {
  display: flex;
  flex-direction: row;
  text-align: left;
  align-items: center;
  justify-content: center;
  border: 3px solid #c4c4c4;
  border-radius: 20px;
  background-color: white;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  padding-top: 2%;
  padding-bottom: 2%;
  margin-top: 3%;
  margin-bottom: 3%;
}

.result-display p {
  font-size: small;
  text-align: left;
  padding: 0;
}

.admin-search-result-display {
  margin-top: 3%;
  margin-bottom: 3%;
}

.search-result-display {
  margin-top: 3%;
  margin-bottom: 3%;
  margin-left: 25%;
  margin-right: 3%;
}

#search-result-terms ul {
  padding: 0;
  margin: 0;
}

.search {
  margin-top: 3%;
  filter: drop-shadow(0px 2px 2px rgba(0, 0, 0, 0.25));
}


.small-search:-ms-input-placeholder {
  font-size: 70%;
}


.small-search::placeholder {
  font-size: 70%;
}


.left-sidebar {
  margin-top: 5%;
  width: 25%;
  position: fixed;
  z-index: 1;
  overflow-x: hidden;
  padding-top: 20px;
  top: 0;
  left: 1%;
  color: #1c3e6e;
}

.left-sidebar p {
  padding-top: 20px;
}

.submit-button {
  background-color: #1c3e6e;
  color: white;
  border: none;
  border-radius: 15px;
  border: none;
  height: 3em;
  width: 7em;
  margin-bottom: 1em;
  margin-top: 1em;
}

#search-results .text-input {
  width: 60%;
}

.text-input {
  width: 40%;
  height: 3em;
  border-radius: 15px;
  border: 2px solid #c4c4c4;
  margin-top: 1%;
  margin-right: 1%;
  margin-left: 1%;
  padding-right: 2%;
  margin-bottom: 10%;
  padding-left: 2%; 
}


.minors-list {
  background-color: #F1F6FD;
  border-radius: 15px;
  text-align: left;
  margin: 0 10%;
  padding: 5%;
}

.minors-list h4 {
  margin-bottom: 1em;
}

.minors-list ul {
  margin: 0;
}


label {
  font-size: large;
}

label input {
  margin-left: 0.5em;
  margin-right: 0.5em;
}



.label {
    background-color: #F1F6FD;
    color: #1C3E6E;
    border-radius: 15px;
    margin-bottom: 3%;
    height: 4em;
    justify-content: center;
    text-align: center;
    align-items: center;
    display: flex;
    flex-direction: row;
}
