* {
    font-family: 'Fredoka One', cursive;
    letter-spacing: 3px !important;
    color: #1C3E6E;
    overflow-x: hidden;
}

p {
    color: #8198B8;
}

.minor-cert-page-content {
    background-color: white;
}

.minors-heading {
    background-color: #F1F6FD;
    border-radius: 20px;
    margin: 10px 10px 10px 10px;
    padding: 10px;
}

.certs-heading {
    background-color: #F1F6FD;
    border-radius: 20px;
    margin: 10px 10px 10px 10px;
    padding: 10px;
}

.minors-template-card {
    border-radius: 10px;
    padding: 3%;
    margin-top: 5%;
    background-color: white;
}

.certs-template-card {
    border-radius: 10px;
    padding: 3%;
    margin-bottom: 20%;
    background-color: white;
}

.minor-certificates-template {
    padding: 3%;
}

.minors-col-item,
.certs-col-item {
    padding: 1.5%;
    margin: 10px 10px 10px 10px;
    border: 3px solid #c4c4c4;
    border-radius: 20px;
    background-color: white;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.minors-col-item h3,
.certs-col-item h3 {
    padding: 1%;
    font-size: large;
    align-items: center;
    justify-content: center;
    text-align: center;
    overflow-wrap: break-word;
}
.App {
  text-align: center;
}

.App-logo {
  height: 10vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.footer img {
  /* make the footer appear in the centre of the page
  */
  background-color: transparent;
  text-align: center;
  width: 35%;
}

